import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./assets/font/iconfont.css";
import { Tabbar, TabbarItem, Swipe, SwipeItem, Toast, Badge, Checkbox, CheckboxGroup, SubmitBar, PullRefresh, List, Uploader, DatetimePicker, SwipeCell, Overlay, ShareSheet } from "vant";

createApp(App)
    .use(store)
    .use(router)
    .use(SubmitBar)
    .use(PullRefresh)
    .use(List)
    .use(Uploader)
    .use(DatetimePicker)
    .use(Tabbar)
    .use(TabbarItem)
    .use(Swipe)
    .use(Toast)
    .use(Badge)
    .use(Checkbox)
    .use(CheckboxGroup)
    .use(SwipeCell)
    .use(Overlay)
    .use(ShareSheet)
    .use(SwipeItem)
    .mount("#app");
